        let end = Math.min((this.currentPage+1)*this.pageSize, this.dapps.length)
        return this.dapps.slice(start, end)
      },
      pageNum(){
        return Math.ceil(this.dapps.length / this.pageSize) || 1 ;
      }
    },
    methods: {
      nextPage(){
        if (this.currentPage >= this.pageNum - 1)
        {
          this.currentPage= 0;
          return
        }
        this.currentPage++;
      },
      prePage(){
        if (this.currentPage <= 0)
        {
          this.currentPage=this.pageNum-1;
        return
        }
        this.currentPage--;
      },
      toPage(page){
        this.currentPage = page;
      }
